<template>
  <div id="SecooCase">
    <div id="all-area" class="all-section">
      <Header></Header>
      <!-- Page Header AREA START -->
      <section
        class="page-head page-bg page-champion-bg page-secoo-bg"
        style="background-image: url('./static/lib/image/secoo/sk-3.png')"
      >
        <div class="page-title">{{titlecase}}</div>
        <div class="page-champion-content-border"></div>
        <p v-for="(item,index) in titlecaselist" :key="index" :class="item.classname">{{item.p}}</p>
      </section>
      <section class="page-head page-bg page-champion-content">
        <h1 class="page-champion-content-title"></h1>
        <div class="page-champion-content-border"></div>
        <img src="../../static/lib/image/secoo/sk-1.png" class="page-champion-content-img1">
      </section>
      <section
        class="page-head page-bg page-secoo-summary"
        style="background-image: url('./static/lib/image/secoo/sk-2.png')"
      >
        <div class="container">
          <div class="row">
            <div class="col-md-6 col-sm-6"></div>
            <div class="col-md-6 col-sm-6" style="margin-top:130px;">
              <h1 style="font-weight: bold;font-size: 60px;margin-bottom: 30px;">{{title}}</h1>
              <div class="page-des-4-border"></div>
              <p
                v-for="(item,index) in zhixingyoushi"
                :key="index"
                style="color: #fff;font-size: 16px;"
              >{{item.p}}</p>
            </div>
          </div>
        </div>
      </section>
      <!-- Footer AREA START -->
      <Footer></Footer>
    </div>
  </div>
</template>

<script>
import Header from "./public/Header";
import Footer from "./public/Footer";
export default {
  name: "SecooCase",
  data() {
    return {
      titlecase: "",
      titlecaselist: [
        { classname: "page-time", p: "" },
        {
          classname: "page-des1",
          p: ""
        },
        {
          classname: "page-des2",
          p: ""
        },
        {
          classname: "page-des3",
          p: ""
        }
      ],
      title: "",
      zhixingyoushi: [
        { p: "KOLKOL100%" },
        { p: "" },
        { p: "KOL" },
        { p: "&" }
      ]
    };
  },
  components: {
    Header,
    Footer
  },
  methods: {}
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
</style>



